import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Auth from '@/components/Auth'
import Profile from '@/components/profile/Profile'
import EditProfile from '@/components/profile/EditProfile'
import Grid from '@/components/addit/Grid'
import Adm from '@/components/profile/Adm'
import Document from '@/components/docs/Document'
import EditDocument from '@/components/docs/EditDocument'
import AddDoc from '@/components/docs/AddDoc'
import NotFound from '@/components/NotFound'
import Help from '@/components/Help'
import store from '../store'
import VeeValidate from 'vee-validate';

Vue.use(Router)
Vue.use(VeeValidate);

const ifNotAuthenticated = (to, from, next) => {
	if (!store.getters.isAuthenticated) {
		next()
		return
	}
	next('/')
}
  
const ifAuthenticated = (to, from, next) => {
	if (store.getters.isAuthenticated) {
		next()
		return
	}
	next('/auth')
}

const ifAdm = (to, from, next) => {
	if (store.getters.getProfile.is_staff) {
		next()
		return
	}
	next('/')
}

const router = new Router({
	mode: 'history',
	base: '/app',
	routes: [
		{
			path: '/',
			// name: 'main',
			component: Main,
			children: [
				{
					path: '',
					redirect: '/documents/all',
				},
				{
					path: 'documents/:id',
					meta: { title: ',  ' }, 
					component: Grid,
					props: {
						columns: [
							{ key: 'reg', title: '' },
							{ key: 'title', title: '' },
							{ key: 'full_name', title: '' },
							{ key: 'date_doc', title: ' ' },
						],
					} 
				},
			],
			beforeEnter: ifAuthenticated,
		},
		{
			path: '/auth',
			name: 'auth',
			meta: { title: ',  ' }, 
			component: Auth,
			beforeEnter: ifNotAuthenticated,
		},
		{
			path: '/help',
			name: 'help',
			meta: { title: ',  ' },
			component: Help,
		},
		{
			path: '/addDoc',
			name: 'addDoc',
			meta: { title: ' ,  ' },
			component: AddDoc,
			beforeEnter: ifAuthenticated,
		},
		{
			path: '/document/:id',
			name: 'document',
			meta: { title: ',  ' },
			component: Document,
			props: true,
			beforeEnter: ifAuthenticated,
		},
		{
			path: '/document/:id/edit',
			name: 'editDocument',
			meta: { title: ' ,  ' },
			component: EditDocument,
			props: true,
			beforeEnter: ifAuthenticated,
		},
		{
			path: '/profile',
			redirect: '/profile/notif',
			component: Profile,
			children: [
				{
					path: 'notif',
					name: 'notif',
					meta: { title: ',  ' }, 
					component: Grid,
					props: {
						id: "notif",
						columns: [
							{ key: 'initiator', title: ''},
							{key: 'title', title: '' },
							{key: 'message', title: ''},
							{key: 'date_notif', title: ''},
							{key: 'file_cabinet', title: '' },
						],
					} 
				},
				{ 
					path: 'edit', 
					meta: { title: ' ,  ' }, 
					component: EditProfile 
				},
				{ 
					path: 'adm', 
					meta: { title: ' ,  ' }, 
					component: Adm, 
					beforeEnter: ifAdm, 
				},
			],
			beforeEnter: ifAuthenticated,
		},
		{
			path: '/404',
			meta: { title: '  ,  ' },
			component: NotFound 
		},
		{ 
			path: '*',
			redirect: '/404',
		},
	]
})

router.beforeEach((to, from, next) => {
	document.title = to.meta.title
	next()
})

export default router